import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";
import "./App.css";
import Profile from "./pages/Profile";
import SendMessage from "./pages/SendMessage";
import LIFFWindow from "./pages/LIFFWindow";
import Header from "./pages/components/Header";

console.log("process.env.PUBLIC_URL", process.env.PUBLIC_URL);

class App extends Component {
  render() {
    return (
      <div className="app">
        <header className="app-header">
          <h3 className="app-title">React LIFF Boilerplate</h3>
        </header>
        <Header />
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <div>
            <Route exact path={process.env.PUBLIC_URL} component={Profile} />
            <Route
              path={process.env.PUBLIC_URL + "/message"}
              component={SendMessage}
            />
            <Route
              path={process.env.PUBLIC_URL + "/window"}
              component={LIFFWindow}
            />
          </div>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
